<template>
  <!--做出input输入点击之后在这里查询歌曲-->
  <div class="search1">
    <!-- 搜索按钮 -->
    <div class="search_1">
      <!-- 返回按钮 -->
      <div class="img3" @click="return_first">
        <img style="width:80%;" src="../../../assets/icon/zuojiantou.png" alt />
      </div>
      <!-- 搜索图片 -->
      <div class="img img1">
        <img style="width:100%" src="../../../assets/icon/sousuo.png" alt />
      </div>
      <!-- 输入框 -->
      <input
        v-model="kwords"
        @keyup.13="search"
        v-focus
        style="width:65%;border:none;"
        type="text"
        placeholder="搜索音乐、视频、歌单、歌手、用户"
      />
      <!-- 用户语音图片 -->
      <div class="img img2" @click="search">
        <img style="width:100%;" src="../../../assets/icon/yuyin1.png" alt />
      </div>
    </div>

    <!-- 这里用来做输入的搜索关键词 -->
    <div :style="nono" v-for="(items,i) in options" :key="i">
      <div class="musiclist">
        <div class="musiclistright" @click="goto(items.id);geter(items.song)">
          <div class="musicleftsize">
            <div style="width:1.7rem;margin-top:0.3rem;margin-right:0.5rem;">
              <img style="width:100%;" src="../../../assets/icon/sousuo.png" alt />
            </div>
            <span>{{items.song}}</span>
            <p style="margin-top:0.5rem;">{{items.sname}}</p>
          </div>
          <p style="font-size:2rem;margin-top:0.5rem;">&gt;</p>
        </div>
      </div>
    </div>

    <!-- 第二个显示热门搜索 -->
    <div :style="no">
      <div class="search_second">
        <p>热门搜索</p>
      </div>
      <div class="search_span">
        <span
          @click="gogo(lis.id,lis.song)"
          style="display:inline;"
          v-for="(lis,i) in liss"
          :key="i"
        >{{lis.song}}</span>
      </div>

      <!-- 第三个显示搜索历史 -->
      <div class="search_second">
        <p>搜索历史</p>
        <div @click="del" style="width:2rem;">
          <img style="width:100%;" src="../../../assets/icon/shanchu.png" alt />
        </div>
      </div>
      <div class="search_span">
        <span @click="ggo(his.id.id)" style="display:inline;" v-for="(his,i) in histroy" :key="i">{{his.id.song}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { MessageBox } from "mint-ui";
import { mapState } from "vuex";
export default {
  data() {
    return {
      kwords: "",
      no: "display:block;",
      nono: "display:none;",
      list: [],
      options: [],
      src: "",
      liss: []
    };
  },
  computed: {
    ...mapState(["histroy"])
  },
  methods: {
    gogo(id,song) {
      this.$router.push("/player/" + id);
      window.sessionStorage.setItem("lid", id);
      this.$store.commit("histroy", {song,id});
    },
    goto(id) {
      this.$router.push("/player/" + id);
      window.sessionStorage.setItem("lid", id);
    },
    del() {
      MessageBox.confirm("Are you sure?").then(action => {
        if (action == "confirm") {
          this.$store.commit("clearhistroy");
        }
      });
    },
    return_first() {
      this.$router.push("/");
    },
    getr() {
      var url = "/getmusiclist";
      this.axios.get(url).then(result => {
        this.list = result.data;
      });
    },
    search() {
      //使用回车跳转，点击回车获取到当前检索出来的歌曲列表，将列表截取第一位得到id值，使用id跳转到播放页；
      let option = this.options;
      for(let o in option){
        let i = option.slice(0,1);
        let id=i[0].id;
        this.$router.push('/player/'+id)
      }

    },
    ggo(id){
      this.$router.push("/player/" + id);
    }
  },
  // 这是自定义的指令,自动获取焦点
  directives: {
    focus: {
      inserted(elem) {
        elem.focus();
      }
    }
  },
  created() {
    this.getr();
  },
  watch: {
    kwords() {
      // 此处还有bug，当输入一个正确的显示后，再次变更输入框中的值还满足条件会同时出现相同的歌名
      if (this.kwords) {
        this.no = "display:none;";
        this.nono = "display:block;";
        // 这是过滤用户输入的数据
        this.list.filter(item => {
          var option = item.song.includes(this.kwords);
          if (option == true) {
            this.options.push(item);
          }
        });
      } else {
        this.no = "display:block;";
        this.nono = "display:none;";
        this.options = [];
      }
      // this.options = this.kwords?this.list.filter(item => item.song.includes(this.kwords)):this.list;
    },
    // 利用slice将截取后的值放在数组中，让热门搜素遍历这个数组即可
    list() {
      for (let lis in this.list) {
        if (this.list.length > 20) {
          this.liss = this.list.slice(0, 10);
        }
      }
    }
  }
};
</script>

<style scoped>
.search_1 {
  display: flex;
  padding: 1rem;
}
.img {
  background: #fff;
  width: 10%;
  height: 2.4rem;
  padding: 0.3rem;
}
.img1 {
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
}
.img2 {
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
}
.img3 {
  width: 15%;
  margin-top: -0.2rem;
}

.search_second {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
.search_second > p {
  font-size: 1.5rem;
  color: #000;
  font-weight: 600;
}
.search_span {
  display: flex;
  flex-wrap: wrap;
}
.search_span > span {
  background: #fff;
  padding: 0.7rem;
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
  margin: 0.5rem;
}

/* 第二块 */
.musiclist {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #b8ddb9;
  margin-bottom: 1rem;
}
.musiclistleft {
  width: 30%;
}
.musiclistright {
  flex: 1;
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
.musicleftsize {
  display: flex;
  align-items: center;
  justify-content: center;
}
.musicleftsize > p {
  padding-left: 2rem;
}
</style>
